*{padding: 0; margin: 0;}

$color1: #1A5D1A;
$color2: #F1C93B;

@mixin smallScreen{
  @media (max-width: 1250px){ 
    @content;
  }
}

a{text-decoration: none; color: #000000;}

.main{
  background-color: $color1;
  position: relative;
  width: 100%;

  .container{

    @include smallScreen {width: 100%;}

    width: 1250px; height: 100vh;
    margin: 0 auto;
    

    .content{
      display: flex; justify-content: center; align-items: center;
      flex-direction: column;
      height: 100%;
      width: 100%;
      padding: 0px 10px; box-sizing: border-box;

      

      .result{
        
        width: 100%; height: 100px;
        @include smallScreen {width: 100%}
        border-radius: 5px;
        font-size: 24px; font-weight: bold;
        text-align: center;
        background-color: #fff;
        display: flex;
        align-items: center; justify-content: center;

        .result-monitor{padding: 0 20px;}

      }

      .btn-zone{
        display: flex;
        button{
          display: block;
          padding: 10px 20px; border-radius: 5px;
          background-color: $color2; border: none;
          cursor: pointer; transition: background-color 0.1s;
          &:hover{background-color: black; color: white;}
          margin: 30px 10px 0 10px;
        }
      }

      .setting{
        padding: 20px 0; color: white;
        width: 100%;
        display: flex; justify-content: space-between;
        align-items: center;

        select{
          display: block;
          padding: 10px 20px; border-radius: 5px;
          font-size: 18px;
        }

        .check-monitor{

          padding-left: 10px;

          .fa-user{
            font-size: 20px;
            margin: 0 5px;
            color: grey;
          }

          .active{color: $color2;}

        }
      }


    }

    .menu{
      position: fixed; top: 0;
  
      .menu-wrap{
        display: flex;
        padding: 20px;
  
        a.menu-btn{
          padding: 10px 20px; background-color: $color2;
          border-radius: 5px; margin-right: 10px;
          cursor: pointer; transition: 0.2s;
          &:last-child{margin-right: 0;}
          &:hover{background-color: white; color: black; font-weight: bold;}
        }
  
        .menu-btn.active{
          background-color: #fff;
        }
      }
    }

  }

}

.full-modal{

  width: 100%; height: 100vh;
  position: fixed; top: 0;
  background-color: rgba($color: #000000, $alpha: 0.5);
  display: flex; justify-content: center; align-items: center;

  z-index: -1; opacity: 0;

  .custom{

    width: 80%; height: 80%; padding: 20px;
    background-color: $color2; border-radius: 10px;
    display: flex; flex-direction: column;
    justify-content: center; align-items: center;

    .mission-list{

      width: 90%; height: 100%;
      display: flex; align-items: center;
      flex-direction: column;

      textarea{
        width: 100%; height: 90%;
        padding: 20px; border-radius: 10px;
      }

    }

    button{
      svg{margin-right: 5px;}
      padding: 5px 10px; margin: 5px 0;
      background-color: $color1; border: 1px solid black;
      color: white; border-radius: 5px;
      cursor: pointer; transition: 0.1s;
      &:hover{background-color: white; color: black;}
      display: inline;
    }
    button.add{margin-right: 10px;}
  }
  

}