* {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: #000000;
}

.main {
  background-color: #1A5D1A;
  position: relative;
  width: 100%;
}
.main .container {
  width: 1250px;
  height: 100vh;
  margin: 0 auto;
}
@media (max-width: 1250px) {
  .main .container {
    width: 100%;
  }
}
.main .container .content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 0px 10px;
  box-sizing: border-box;
}
.main .container .content .result {
  width: 100%;
  height: 100px;
  border-radius: 5px;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1250px) {
  .main .container .content .result {
    width: 100%;
  }
}
.main .container .content .result .result-monitor {
  padding: 0 20px;
}
.main .container .content .btn-zone {
  display: flex;
}
.main .container .content .btn-zone button {
  display: block;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: #F1C93B;
  border: none;
  cursor: pointer;
  transition: background-color 0.1s;
  margin: 30px 10px 0 10px;
}
.main .container .content .btn-zone button:hover {
  background-color: black;
  color: white;
}
.main .container .content .setting {
  padding: 20px 0;
  color: white;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main .container .content .setting select {
  display: block;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 18px;
}
.main .container .content .setting .check-monitor {
  padding-left: 10px;
}
.main .container .content .setting .check-monitor .fa-user {
  font-size: 20px;
  margin: 0 5px;
  color: grey;
}
.main .container .content .setting .check-monitor .active {
  color: #F1C93B;
}
.main .container .menu {
  position: fixed;
  top: 0;
}
.main .container .menu .menu-wrap {
  display: flex;
  padding: 20px;
}
.main .container .menu .menu-wrap a.menu-btn {
  padding: 10px 20px;
  background-color: #F1C93B;
  border-radius: 5px;
  margin-right: 10px;
  cursor: pointer;
  transition: 0.2s;
}
.main .container .menu .menu-wrap a.menu-btn:last-child {
  margin-right: 0;
}
.main .container .menu .menu-wrap a.menu-btn:hover {
  background-color: white;
  color: black;
  font-weight: bold;
}
.main .container .menu .menu-wrap .menu-btn.active {
  background-color: #fff;
}

.full-modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  opacity: 0;
}
.full-modal .custom {
  width: 80%;
  height: 80%;
  padding: 20px;
  background-color: #F1C93B;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.full-modal .custom .mission-list {
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.full-modal .custom .mission-list textarea {
  width: 100%;
  height: 90%;
  padding: 20px;
  border-radius: 10px;
}
.full-modal .custom button {
  padding: 5px 10px;
  margin: 5px 0;
  background-color: #1A5D1A;
  border: 1px solid black;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.1s;
  display: inline;
}
.full-modal .custom button svg {
  margin-right: 5px;
}
.full-modal .custom button:hover {
  background-color: white;
  color: black;
}
.full-modal .custom button.add {
  margin-right: 10px;
}/*# sourceMappingURL=style.css.map */